<template>
<div>
  <div class="input_box">
    <input type="text" placeholder="请输入工区名称" v-model="name">
    <i class="iconfont close_icon" v-if="name" @click="name=''"></i>
  </div>
  <div class="btn_box">
      <mt-button type="primary" size="large" @click="toPage">保存</mt-button>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      name: ''
    }
  },
  methods: {
    toPage () {
      if (!name) return this.$toast('工区名称不能为空')
      this.$router.push({name: 'EditAddress', query: {name: name}})
    }
  }
}
</script>
<style lang="scss" scoped>
.input_box {
  margin: 15px 15px 0;
  border: 1px solid #eee;
  padding: 5px;
  box-sizing: border-box;
  border-radius: 5px;
  height: 40px;
  display: flex;
  align-items: center;
  input {
    border: none;
    width: calc(100% - 30px);
    text-indent: 10px;
    flex: 1;
  }
  i {
    width: 20px;
    color: #999;
  }
}
.btn_box {
  margin: 20px 15px 0;
  text-align: center;
}
</style>



